main {
  padding: 20px;
}

main h3 {
  margin: 0 0 0 20px;
  font-size: 24px;
  font-weight: bold;
  color: #333333;
}

main p {
  font-size: 16px;
  line-height: 2;
  margin: 10px 0;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

main p span {
  color: blue;
}

main ul {
  display: grid;
  grid-template-columns: repeat(4, 1fr); 
  grid-template-rows: repeat(4, 1fr);
  padding: 0; 
}

@media (max-width: 768px) {
  main ul {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(8, 1fr);
    padding: 0;
  }
}

main ul li {
  list-style: none;
}