.post-detail {
  padding: 20px;
  margin: 0 auto;
  max-width: 700px;
}

.post-detail h3 {
  margin: 0 0 10px;
  font-size: 24px;
  font-weight: bold;
  color: #333;
}

.post-detail p {
  margin: 10px 0;
  font-size: 16px;
  line-height: 1.5;
}

.post-detail li {
  list-style: none;
}

.post-detail button {
  background-color: #ddd;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
}

.post-detail button:hover {
  background-color: #ccc;
}